



















export default {
	data() {
		return {
			positionTop: 0,
			phone: "",
			showPassword: true,
			password: ''
		};
	},
	methods: {
		initPosition() {
			/**
			* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			* 反向使用 top 进行定位，可以避免此问题。
			*/
			this.positionTop = uni.getSystemInfoSync().windowHeight - 140;
		},
		goWechatLogin() {
			uni.navigateTo({
				url: '../../login-wechat/login-wechat'
			})
		},
		changePassword: function () {
			this.showPassword = !this.showPassword;
		},
		goIndex() {
			var _this = this
			var reg = /\w{6,}/  // 字母数字下划线，最少六位
			if(reg.test(this.password)) {
				uni.request({
					url: 'http://www.aikm.cn/api/retrieve',
					method: 'POST',
					data: {
						phone: _this.phone,
						password: _this.password
					},
					success: res => {
						// console.log(JSON.stringify(res.data))
						
						if(res.data.code == 1) {
							uni.showToast({
								title: res.data.msg,
								duration: 3000,
								icon: 'success',
							});
							setTimeout(function() {
								uni.redirectTo({
									url: '../../login-phone/login-phone'
								});
							}, 800)
						}
					}
				});
			} else {
				uni.showToast({
					title: '请输入不少于6位的字母数字下划线',
					duration: 3000,
					icon: 'none',
				});
			}
			
		}
	},
	onLoad(option) {
		this.initPosition();
		this.phone = option.phone;
	}
}



